.water-system {
    margin-top: 5rem;
    margin-bottom: 3rem;
    width: 100%;
    height: 100%;

    &__banner {
        width: 100%;
        height: 60vh;
        background-image: url('../assets/images/fire-alarms/Web03.jpg');
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
        background-attachment: fixed;
        position: relative;
        animation: fade .4s ease-in-out;

        &::after {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 60vh;
            background: linear-gradient(rgba(0,0,0,0.7), rgba(187,187,187,0.7)) 50% 50% / cover fixed;

        }

        .water-system__banner-text {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            color: #fff;
            z-index: 9;
            font-size: 1.6rem;
            width: 100%;
            text-align: center;
            letter-spacing: 6px;
            text-transform: uppercase;
        }
    }

    &__body {
        width: 70%;
        max-width: 1200px;
        margin: 0 auto;
        margin-top: 2rem;
        margin-bottom: 4rem;

        ul {
            margin-bottom: 2.5rem;
        }

        button {
            width: 23rem;
            padding: 1rem;
            font-size: 1.2rem;
            background: transparent;
            border: 2px solid #000;
            position: relative;
            cursor: pointer;

            &.active {
                background: #000;
                color: #fff;
            }

            &::after {
                content: '+';
                display: block;
                position: absolute;
                right: .5rem;
                top: .8rem;
                font-size: 1.5rem;
                font-weight: 700;
            }

            &.active::after {
                content: '-';
                display: block;
                position: absolute;
                right: .5rem;
                top: .7rem;
                font-size: 1.5rem;
                font-weight: 700;
            }
        }

        .water-systems-burb {
            margin-top: 2.5rem;
            margin-bottom: 2.5rem;
        }

        p {
            margin: 0;
        }

        .visible {
            opacity: 1;
            visibility: visible;
            height: 100%;
            padding: 1rem;
            color: #000;
            border: 1px solid #000;
            width: 30rem;
            overflow: scroll;
            transition: opacity .5s ease-in-out;
        }

        .hidden {
            opacity: 0;
            visibility: hidden;
            height: 0;
        }
    }
}

@media (max-width: 990px) {

    .water-system {
        &__body {
            width: 85%;

            .visible {
                width: 100%;
            }

            button {
                width: 90%;
            }
        }
    }
}

@keyframes fade {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}